// VentaDetalleModal.tsx
'use client';
import React from 'react';
import { VentaServicio } from '@/entities/venta';
import { formatCurrency, formatDate } from '@/lib/logistica/formatters';

type Props = {
  venta: VentaServicio;
  onClose: () => void;
};

const VentaDetalleModal = ({ venta, onClose }: Props) => {
  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
      <div className="bg-white p-6 rounded-lg max-w-md w-full space-y-4 shadow-lg">
        <h2 className="text-xl font-bold text-gray-800">Detalle de Venta</h2>
        <p><strong>Cliente:</strong> {venta.cliente.nombre}</p>
        <p><strong>Fecha:</strong> {formatDate(venta.fechaRegistro)}</p>
        <ul className="list-disc pl-5">
          {venta.detalles.map(d => (
            <li key={d.idDetalleServicio}>
              {d.nombreServicio} - {formatCurrency(d.costoTotal)}
            </li>
          ))}
        </ul>
        <p><strong>Descuento:</strong> {formatCurrency(venta.descuento)}</p>
        <p><strong>Total:</strong> {formatCurrency(venta.costoTotal)}</p>
        <button onClick={onClose} className="mt-4 bg-gray-600 text-white px-4 py-2 rounded">
          Cerrar
        </button>
      </div>
    </div>
  );
};

export default VentaDetalleModal;
