'use client';
import React from 'react';
import { VentaServicio } from '@/entities/venta';
import { formatCurrency, formatDate } from '@/lib/logistica/formatters';

type Props = {
  venta?: VentaServicio;
  onClose: () => void;
};

const VentaDetalleModal: React.FC<Props> = ({ venta, onClose }) => {
  if (!venta || !venta.cliente) return null; // ðŸ’¥ ProtecciÃ³n doble

  return (
    <div className="fixed inset-0 bg-black bg-opacity-40 flex items-center justify-center z-50">
      <div className="bg-white w-full max-w-md rounded-lg shadow-lg p-6 relative">
        <button
          onClick={onClose}
          className="absolute top-2 right-2 text-gray-500 hover:text-gray-800"
        >
          âœ•
        </button>

        <h2 className="text-xl font-semibold mb-4 text-gray-800">Detalle de Venta</h2>

        <div className="space-y-2 text-sm text-gray-700">
          <p><strong>ID Venta:</strong> {venta.idVentaServicio}</p>
          <p><strong>Cliente:</strong> {venta.cliente.nombre}</p>
          <p><strong>Fecha:</strong> {formatDate(venta.fechaRegistro)}</p>

          <div>
            <strong>Servicios:</strong>
            <ul className="list-disc list-inside mt-1">
              {venta.detalles?.map((d) => (
                <li key={d.idDetalleServicio}>
                  {d.nombreServicio} â€“ {formatCurrency(d.costoTotal)}
                </li>
              ))}
            </ul>
          </div>

          <p><strong>Descuento:</strong> {formatCurrency(venta.descuento)}</p>
          <p><strong>Total:</strong> {formatCurrency(venta.costoTotal)}</p>
        </div>
      </div>
    </div>
  );
};

export default VentaDetalleModal;
